*,
*::before,
*,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', Tahoma, sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #fff;
  background-color: #000;
  box-sizing: border-box;
}

html {
  font-size: 64px;
}

.container {
  max-width: 940px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}

.row {
  display: flex;
}

.btn {
  padding: 9.5px 13.5px;
  background: #e45028;
  border: none;
  outline: none;
  border-radius: 4px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}

.section-title {
  font-family: "Gagalin", Arial, sans-serif;
  font-size: 72px;
  margin: 0;
  line-height: 64px;
  margin-bottom: 80px;
  color: #f9b43b;
}

