.section {
  height: 100vh;
  min-height: 650px;
}

.main {
  background: url("../img/background-section1.jpg");
  background-size: cover;
}

.main-header {
  padding-top: 10px;
}

.header-main-row {
  align-items: center;
}

.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  margin-bottom: 13px;
  margin-right: -24px;
  list-style: none;
}

.navigation__item {
  position: relative;
  padding-right: 16px;
  margin-right: 20px;
}

.navigation__item::after {
  position: absolute;
  content: '';
  top: 10px;
  right: -3px;
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
}

.navigation__item:last-child::after {
  display: none;
}

.navigation__link {
  text-decoration: none;
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 500;
}

.logo {
  margin-right: auto;
}

.order {
  margin-bottom: 12px;
  margin-right: -25px;
}

.promo__desc {
  font-family: "Lifehack", Arial, sans-serif;
}

.promo__title {
  font-family: "Gagalin", Arial, sans-serif;
  font-size: 72px;
  color: #f9b43b;
  margin-bottom: 40px;
}

.promo__img {
  margin-left: 20px;
  margin-top: -14px;
}

.promo__content {
  font-size: 65px;
  color: #fff;
  line-height: 60px;
  margin-top: 120px;
  margin-left: 13px;
}

.features {
  background: url("../img/background-features.png");
  background-size: cover;
  padding-top: 75px;
  padding-bottom: 88px;
}

.features-row {
  justify-content: space-between;
  align-items: baseline;
}

.section-title--pos_center {
  color: #2f3234;
  text-align: center;
}

.features__title {
  color: #2f3234;
  font-family: "Lifehack", Arial, sans-serif;
  font-size: 24px;
  text-align: center;
  margin-bottom: 14px;
  margin-left: 3px;
}

.features__desc {
  color: #2f3234;
  font-family: "Ubuntu", Arial, sans-serif;
  font-size: 14px;
  text-align: center;
}

.feature__img {
  text-align: center;
  margin-bottom: 30px;
}

.slider {
  background: url("../img/background-slider.png");
  background-size: cover;
}

.slide {
  padding-top: 90px;
}

.slide-img__btn {
  position: relative;
  padding: 38.5px 24px;
  background-color: rgba(227, 80, 40, 0.4);
  text-decoration: none;
  font-family: Gagalin;
  font-size: 14px;
  color: #fff;
  border-radius: 4px;
}

.slide-img__btn::after {
  position: absolute;
  content: '';
  width: 50px;
  height: 50px;
  top: 10px;
  left: 0;
  background: url("../img/burger.svg") no-repeat;
}

@media screen and (max-width: 768px) {
  .navigation__list {
    display: none;
  }

  .promo-row {
    flex-direction: column;
    text-align: center;
  }

  .promo__content {
    margin-top: 50px;
    padding: 0 15%;
  }
}